import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import {
  MainComponent,
  HomeComponent,
  SinceWeMeetComponent
} from './components';

const routes: Routes = [
  {
    path: '',
    component: MainComponent,
    children: [
      {
        path: '',
        children: [
          { path: '', component: HomeComponent },
        ]
      }
    ]
  },
  {
    path: 'em',
    component: SinceWeMeetComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class KyozRoutingModule { }



