import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import 'rxjs/add/observable/fromEvent';

/* Angular dependencies */
import {
  MatToolbarModule,
  MatButtonModule,
  MatIconModule,
  MatTooltipModule

} from '@angular/material';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FlexLayoutModule } from '@angular/flex-layout';

/* User dependencies */
import { KyozRoutingModule } from './kyoz-routing.module';

import {
  MainComponent,
  HomeComponent,
  AvatarFlipperComponent,
  TwinkingStarsBackgroundComponent,
  SinceWeMeetComponent
} from './components';

import { SampleDirectiveDirective } from './directives';
import { SamplePipePipe } from './pipes';
import { SampleServiceService } from './services';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FlexLayoutModule,
    KyozRoutingModule,
    // Material Module
    MatToolbarModule,
    MatButtonModule,
    MatIconModule,
    MatTooltipModule
  ],
  declarations: [
    MainComponent,
    HomeComponent,
    AvatarFlipperComponent,
    TwinkingStarsBackgroundComponent,
    SinceWeMeetComponent,
    SampleDirectiveDirective,
    SamplePipePipe,
  ],
  providers: [SampleServiceService]
})
export class KyozModule { }



